import React from "react";
import rmclogo from "../images/rmclogo.png";
import rmclogo2 from "../images/rmclogo2.jpg";
import './Reciept.css'
import swachhBharat from "../images/swachhBharat.png";
import "../../../../../index.css";
import { indianAmount, nullToNA } from "../../../../../Components/Common/PowerUps/PowerupFunctions";
import QrCode from "../../../../Trade/tradeComponent/QrCode";
import signature from './signature.png'

class FamReciept extends React.Component {
  render() {

    const famDetails = this.props?.famDetails

    let notes = [
      "If there is any objection against the tax prescribed by the corporation, under the prescribed provision of Section 167 (1) of the Jharkhand Municipal Act, 2011, within 30 (thirty) days of receipt of this assessment Memo can file an objection in the prescribed form.",
      "The list of tax assessment is displayed on the Ranchi Municipal Corporation website wwvw.ranchimunicipal.com.",
      "As per clause 13.4 of the Jharkhand Municipal Property Tax (Assessment, Collection and Recovery) Rules 2013, the actual amount of differential tax and one hundred percent penalty on its also payable.",
      "This property tax collected by the Ranchi Municipal Corporation does not confer any legal status on these buildings / structures and / or does not confer any legal rights to its owners / occupiers."
    ]

    const noteList = notes.map((content, index) => {
      return <li>
        <span>{index + 1}.&nbsp;</span>
        <span>{content}</span>
      </li>;
    });

    let holdingNo;

    { (famDetails?.pt_no == '' || famDetails?.pt_no == undefined) ? holdingNo = famDetails?.holding_no?.split('') : holdingNo = famDetails?.pt_no?.split('') }

    const holdingBox = holdingNo?.map((num) => {
      return <div className='px-2 py-1 border-2 border-black text-xs'>{num}</div>
    })

    return (
      <>
        <div id="printableArea">
          <div className="flex items-center justify-center text-black" >
            <div className="container w-[70%] border-2 border-dashed m-2 overflow-x-hidden border-black py-4 px-3">

              <img src={rmclogo} alt="" srcset="" className='absolute top-[20%] left-[27%] backImage opacity-10 ' />

              {/* Logo Heading */}
              <div>
                <div className="flex flex-col justify-center items-center gap-x-4">
                  <img src={rmclogo2} alt="Logo" srcset="" className="h-16 w-16" />
                  <span className="text-3xl font-bold uppercase">Ranchi Municipal Corporation</span>
                </div>
                <div className='w-full flex justify-center mt-2'>
                  <div className='grid grid-cols-6 w-max'>
                    {/* <div className="col-span-6 uppercase border-2 font-bold border-black px-10 py-2 text-xl">SAM Reciept</div> */}
                    <div className="col-span-6 uppercase border-2 font-bold border-black px-8 py-1 text-md">FAM Reciept</div>
                  </div>
                </div>
              </div>

              {/* message */}
              <div>
                <hr className='border-black mt-6 mb-2' />
                <div className='font-semibold text-xs'>
                  Notice regarding property tax as specified in Section 12(B) in conjunction with the Jharkhand Municipal Property Tax (Assessment, Collection, and Recovery) Rules outlined in the Jharkhand Municipal Act of 2011.
                </div>
                <hr className='border-black mb-6 mt-2' />
              </div>

              {/* name address memo no. */}
              <div className='flex justify-between mt-4 font-semibold gap-2'>
                <div className="text-start text-xs w-[65%]">
                  Mr/Mrs/Ms: <span className="font-normal text-xs">{nullToNA(famDetails?.owner_name)} {nullToNA(famDetails?.relation_type)} {nullToNA(famDetails?.guardian_name)}</span> <br />
                  Address: <span className="font-normal text-xs">{nullToNA(famDetails?.prop_address)}</span>
                </div>
                <div className="text-left text-xs w-[35%]">
                  Memo No.: <span className="font-normal">{nullToNA(famDetails?.memo_no)}</span> <br />
                  Date: <span className="font-normal">{nullToNA(famDetails?.memo_date)}</span> <br />
                  Effective: <span className="font-normal">{famDetails?.from_qtr == 1 && <>First</>}{famDetails?.from_qtr == 2 && <>Second</>}{famDetails?.from_qtr == 3 && <>Third</>}{famDetails?.from_qtr == 4 && <>Fourth</>} Quarter {nullToNA(famDetails?.from_fyear)}</span> <br />
                </div>
              </div>

              {/* declaration */}
              <div className='text-xs mt-2'>
                <p className='mb-3'>You are hereby informed that your {(famDetails?.pt_no == '' || famDetails?.pt_no == undefined) ? 'Holding' : 'Property Tax'} No.-</p>
                <div className='flex flex-row'>
                  {holdingBox}
                </div>
                <p className='my-1'>According to the {famDetails?.rule} of this holding is <span className="font-semibold">{indianAmount(famDetails?.arv)}</span>/- after local check made by Ranchi Municipal Corporation in the light of the self assessment deciaration letter
                  made by you for assessment of tax for <span className="font-semibold">Ward No. <span className='font-normal'>{nullToNA(famDetails?.new_ward_no)}</span></span> (<span className="font-semibold">Old Ward No.</span> {nullToNA(famDetails?.ward_no)}) , {indianAmount(famDetails?.arv)}/- is fixed at the place.</p>
                <p>Based on the {famDetails?.rule} determined by the corporation, the first quarter will be taxed in writing with effect from the year 2016-2017.</p>
              </div>

              {/* table */}
              <table className='text-xs font-semibold w-full mt-1'>

                <tr>
                  <td className='border-2 border-black px-1 w-[3%]'>Sl No.</td>
                  <td className='border-2 border-black px-1 w-auto'>Particulars</td>
                  <td className='border-2 border-black px-1 w-[16%]'>Quarter/Financial Year</td>
                  <td className='border-2 border-black px-1 w-[16%]'>Based on the Self Assessment</td>
                  <td className='border-2 border-black px-1 w-[16%]'>On the basis of ULB Calculation</td>
                  <td className='border-2 border-black px-1 w-[9%]'>Difference Amount </td>
                  {/* <td className='border-2 border-black px-1 w-[11%]'>100% Penalty of Column 5</td> */}
                </tr>

                {/* <tr>
              <td className="border-2 border-black px-1"></td>
              <td className="border-2 border-black px-1">2</td>
              <td className="border-2 border-black px-1">2</td>
              <td className="border-2 border-black px-1">3</td>
              <td className="border-2 border-black px-1">4</td>
              <td className="border-2 border-black px-1">5</td>
              <td className="border-2 border-black px-1">6</td>
            </tr> */}

                {
                  famDetails?.taxTable?.map((data, index) =>
                    <>
                      <tr>
                        {index != (famDetails?.taxTable?.length - 1) && <td className="border-2 border-black px-1">{index + 1}</td>}
                        <td colSpan={index == (famDetails?.taxTable?.length - 1) && 3} className={`${index == (famDetails?.taxTable?.length - 1) && 'text-end'} border-2 border-black px-1`}>{nullToNA(data?.Particulars)}</td>
                        {index != (famDetails?.taxTable?.length - 1) && <td className="border-2 border-black px-1">{nullToNA(data?.quarterFinancialYear)}</td>}
                        <td className="border-2 border-black px-1">{indianAmount(data?.basedOnSelfAssess)}</td>
                        <td className="border-2 border-black px-1">{indianAmount(data?.basedOnUlbCalc)}</td>
                        <td className="border-2 border-black px-1">{indianAmount(data?.diffAmt)}</td>
                        {/* <td className="border-2 border-black px-1">{data?.diffAmt}</td> */}
                      </tr>
                    </>)
                }

              </table>

              {/* qr & signature */}
              <div className="mt-4 w-full flex flex-row justify-between items-end">

                {/* qr */}
                <div>
                  {/* <img src="" alt="QR" className="h-20 w-20 border"/> */}
                  <QrCode size='70' url={window.location.href} />
                </div>

                {/* signature */}
                <div className='flex flex-col justify-end items-center'>
                  <img src={signature} alt="" srcset="" className='w-[15vw]' />
                  <p className='text-md font-semibold'>Deputy Municipal Commissioner</p>
                  <p className='text-xs'>Ranchi Municipal Corporation, Ranchi</p>
                </div>

              </div>

              {/* note */}
              <div className="text-xs mt-4 ">
                <div className="font-semibold my-2">
                  Note:-
                </div>

                <ol className="w-full flex flex-col gap-x-2 pl-10">
                  {noteList}
                </ol>

              </div>

              {/* bottom logo */}
              <div className='flex justify-center items-center mt-6'>
                <img src={swachhBharat} alt="" className="h-10 opacity-70" />
              </div>

            </div>
          </div>
        </div>
      </>
    )

  }
}

export default FamReciept;
